@import 'components/variables';

.calendario-vuelos {
  .table {
    tr {
      th, td {
        min-width: 100px;
        height: 60px;
        text-align: center;
        vertical-align: middle;
        border: 1px solid $gray-light;
      }
      .static {
        position: absolute;
        width: 100px;
      }
      .blank, .static-blank {
        border: 1px solid white;
        width: 100px;
        background-color: white;
      }
      .first-col {
        
      }
      .bg-title {
        background-color: $violet;
      }
      th {
        color: white;
      }
      td {
        color: $violet;
        cursor: pointer;
      }
      .col1,
      .col2,
      .col3,
      .col4,
      .col5,
      .col6,
      .col7 {
        &:hover {
          color: white !important;
          background-color: $violet !important;
        }
      }
      .select {
        background-color: rgba($color: $violet, $alpha: 0.5);
        color: white;
      }
    }
  }
}